import React from 'react';
import {
    Background,
    ContactsForm,
    ContactsBlock,
    ContactsWrapper,
    ImgBg,
    Input,
    Title,
    Textarea,
    ButtonForm, SubText, SubTitle, InputEmail, InputName
} from "./style";
import Bg from './assets/bg.png'

export const Contacts = () => {

    return (
        <ContactsWrapper>
            <ContactsBlock>
                <Title> ? - </Title>
                <SubTitle>   <span></span>  !</SubTitle>
                <ContactsForm>
                    <InputName type="text" placeholder=''/>
                    <InputEmail type="email" placeholder='E-mail'/>
                    <Input type="phone" placeholder={window.innerWidth >= 426 ? '': 'BY  +375() --'}/>
                    <Textarea placeholder=''/>
                    <SubText>
                           ,         
                             <a href="#">   </a>
                    </SubText>
                    <ButtonForm type={"button"}></ButtonForm>
                </ContactsForm>
            </ContactsBlock>
            <Background>
                <ImgBg src={Bg} alt="Bg"/>
            </Background>
        </ContactsWrapper>
    );
};

