import React, {FC} from 'react';
import {
    Button,
    CustomInput, Errors,
    FormWrapper,
    InputBlock,
    Label,
    ModalWindowWrapper,
    Placeholder,
    RegisterWrapper, SubText,
    Title
} from "./style";
import {useFormik} from "formik";
import {Clouse} from "./assets";

type FormikErrorType = {
    name?: string,
    phone?: string
}

type ModalWindowPropsType = {
    openModal: () => void
}

export const ModalWindow: FC<ModalWindowPropsType> = ({openModal}) => {

    const formik = useFormik({
        initialValues: {
            name: '',
            phone: '',
        },

        validate: (values) => {
            const errors: FormikErrorType = {}

            if (!values.name) {
                errors.name = '    '
            }

            if (!values.phone) {
                errors.phone = '  '
            }

            return errors
        },
        onSubmit: values => {
            console.log(formik.values)
        }
    })

    return (
        <ModalWindowWrapper openModal>
            <RegisterWrapper>
                <Title>    </Title>
                <Clouse onClick={openModal}/>
                <FormWrapper onSubmit={formik.handleSubmit}>
                    <InputBlock>
                        <Label>
                            <CustomInput type='text'
                                         placeholder=' '
                                         {...formik.getFieldProps('name')}
                                         onChange={formik.handleChange}
                            />
                            {formik.touched.name && formik.errors.name &&
                                <Errors>{formik.errors.name}</Errors>}
                        </Label>
                        <Label>
                            <CustomInput type='text'
                                         placeholder='BY  '
                                         {...formik.getFieldProps('phone')}
                                         onChange={formik.handleChange}/>
                            {formik.touched.phone && formik.errors.phone &&
                                <Errors>{formik.errors.phone}</Errors>}
                        </Label>
                    </InputBlock>
                    <SubText>
                           ,         
                                
                    </SubText>
                    <Button type='submit'></Button>
                </FormWrapper>
            </RegisterWrapper>
        </ModalWindowWrapper>
    );
};

